.section-title {
  text-align: center;
  font-size: 3em;
  font-weight: 700;
  margin-bottom: 50px; }

.header {
  margin-bottom: 50px;
  margin-top: 50px;

  h1 {
    font-weight: 700; }

  p {
    color: $asbestos;
    padding: 0 100px; } }

#topper-nav {
  background: $baseColor;
  color: #fff;
  padding: 8px;

  a {
    color: #fff; } }

#main-content {
  overflow: hidden;
  transition: all 0.4s ease;

  .container {
    width: 100%; }

  &.toggle {
    &.in {
      width: 93%;
      max-width: 93%; }

    &.up {
      max-width: 70%;
      width: 70%; } }

  #first-section {
    min-height: 200px;
    overflow: hidden;
    position: relative;
    z-index: 1;

    [class^="col-md"] {
      min-height: 610px;
      padding: 0; }

    .container-fluid {
      padding: 0; }

    .row {
      margin: 0; }

    .slider-content {
      background-image: url("../img/123.png");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center center;
      padding: 25px;

      .slick-slide img {
        min-height: 170px;
        object-fit: cover; }

      .content-title {
        font-size: 2em; }

      .content-subtitle {
        font-size: 1.5em;
        font-weight: 300; }

      .slider-wrapper {
        margin: 50px 25px;

        .slider-inner {
          .item {
            margin-left: 10px;
            margin-right: 10px; }

          .slider-cloned {
            margin: 0; } } } }

    .popover {
      top: (-1px) !important;
      left: (-340px) !important;
      width: 350px !important;
      max-width: 1000px !important;
      color: #000;
      border-radius: 0;
      box-shadow: none !important;
      border: 0;
      padding: 0 !important;

      .popover-title {
        background: #DE1B1B;
        border: none;
        border-radius: 0;
        padding: 13px;
        text-align: center;
        text-transform: uppercase;
        font-weight: 700;
        color: #fff; }

      .popover-content {
        overflow-y: auto;
        overflow-x: hidden;
        min-height: 570px !important;
        max-height: 570px !important; }

      label {
        font-weight: normal; }

      textarea {
        width: 100%; }

      &.left > .arrow {
        border-left-color: transparent;
        top: -100px; } }

    .content-wrapper {
      color: #fff;
      font-size: 16px;

      .row {
        margin-bottom: 20px; }

      .content-desc {
        font-weight: 300;

        .inner-title {
          font-weight: 400; }

        p {
          margin-bottom: 0; } } }

    .icon-bar {
      width: 100%;
      background: $yellow;

      p {
        margin: 0; }

      .item {
        border-bottom: 1px solid darken($yellow, 10%);
        display: block;
        text-align: center;
        padding: 16px;
        transition: all 0.3s ease;
        color: #333;

        &:hover {
          text-decoration: none;
          background-color: darken($yellow, 50%);
          color: #fff; } } }

    .kurs-wrapper {
      padding: 10px 10px;
      margin-top: 20px;

      span:not(.kurs-title) {
        font-weight: normal;
        font-size: 0.7em;
        text-align: right;
        float: right;
        margin-top: 10px; }

      table {
        font-size: 12px;
        border: none;

        td, th {
          text-align: center; }

        thead {
          color: $sunflower;
          text-transform: uppercase;

          th {
            &:first-child {
              border-left: none; }

            &:last-child {
              border-right: none; } } }

        tbody {
          td {
            font-size: 13px;

            &:first-child {
              border-left: none; }

            &:last-child {
              border-right: none; } }

          tr:last-child td {
            border-bottom: none; }

          img {
            height: 20px;
            width: 20px;
            margin-right: 5px;
            margin-bottom: 2px; } } } }

    .information {
      background: $darkColorAlt;
      padding: 25px 30px;

      .title {
        color: $sunflower;
        font-size: 1.2em; }

      .content {
        text-align: justify; } }

    .promo {
      padding: 25px 30px;

      img {
        width: 100%;
        height: 125px;
        object-fit: cover; } } }

  #news-flash {
    color: #fff;

    .title {
      padding: 10px 5px;
      text-align: center;
      background: $baseColor;
      color: #fff;
      width: 13%;
      float: left; }

    .item {
      padding: 10px 30px;
      float: left;
      width: 100%;
      margin-left: 40px;
      display: none;

      a {
        color: #fff; }

      &.active {
        margin-left: 0;
        width: 87%;
        display: block; } } }

  #broker-list {
    background: $clouds;
    padding: 0 0 25px;

    .footer {
      .btn {
        background: $baseColor;
        transition: all 0.3s ease;
        color: $clouds;
        font-weight: 700;
        padding: 10px 50px;
        font-size: 1.2em;
        border-radius: 0;

        &:hover {
          background: darken($baseColor, 15%); } } }

    .item-wrapper {
      margin-bottom: 20px; }

    .image {
      display: block;
      min-width: 100%;
      width: 100%;
      height: 230px;
      object-fit: cover; }

    .overlay {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: $baseColor;
      overflow: hidden;
      height: 0;
      transition: 0.5s ease;
      margin-left: 15px;
      margin-right: 15px; }

    .item-wrapper:hover .overlay {
      height: 100%; }

    .text {
      color: white;
      font-size: 20px;
      overflow: hidden;
      padding: 20px;

      .btn {
        border-radius: 0;
        background: $sunflower;
        color: #000;
        margin-top: 20px; }

      h4 {
        text-align: center;
        font-weight: 700; }

      p {
        font-size: 0.75em;
        font-weight: 300;
        text-align: center; } } }

  #pricing-table {
    background: #fff;
    padding: 50px 0;

    .container {
      width: 1170px; }

    .header {
      h1 {
        font-weight: 700; }

      p {
        color: $asbestos; } }

    .turqoise-item {
      .content-top, .custom-icon {
        background: #DE1B1B; }

      .content-bottom {
        color: #DE1B1B; }

      .subscribe {
        background: #DE1B1B; } }

    .red-item {
      .content-top, .custom-icon {
        background: #ccc;
        color: #333 !important; }

      hr {
        border-bottom: 1px solid rgba(0, 0, 0, 0.7) !important; }

      .content-bottom {
        color: #333; }

      .subscribe {
        background: #ccc;
        color: #333 !important; } }

    .belize-item {
      .content-top, .custom-icon {
        background: $sunflower;
        color: #333 !important; }

      hr {
        border-bottom: 1px solid rgba(0, 0, 0, 0.7) !important; }

      .content-bottom {
        color: #333; }

      .subscribe {
        background: $sunflower;
        color: #333 !important; } }

    .pricing-item {
      text-align: center;

      .garnish {
        display: inline-block;
        margin-top: -41px; }

      .custom-icon {
        box-sizing: content-box;
        padding: 20px;
        height: 40px;
        width: 40px;
        border: 3px solid #eee;
        border-radius: 100%;
        color: #fff;
        vertical-align: middle;
        font-size: 24px;
        display: table-cell;
        text-align: center; }

      &.small {
        .content-bottom {
          padding: 50px 30px 30px; }

        .custom-icon {
          height: 25px;
          width: 25px;
          padding: 12.5px;
          font-size: 18px; }

        .garnish {
          margin-top: -25px; }

        .content-top {
          .title {
            font-size: 20px; }

          .given-price span {
            font-size: 1.5em; } }

        .subscribe {
          font-size: 16px !important; } } }

    .pricing-item {
      .content-top {
        padding: 50px;
        font-size: 30px;
        color: #fff;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;

        p {
          margin: 0; }

        .title {
          font-weight: 700;
          margin-bottom: 0; }

        hr {
          border: 0;
          border-bottom: 1px solid rgba(189, 195, 199, 0.7);
          width: 75%;
          margin: 10px auto; }

        .given-price {
          font-weight: 300;

          span {
            font-weight: 700;
            font-size: 2em; } }

        .given-period {
          font-size: 0.6em;
          font-weight: 300; } }

      .content-bottom {
        margin-top: -50px;
        background: #eee;
        font-size: 18px;
        padding: 50px 50px 30px;
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;

        .feature {
          max-height: 115.5px;
          overflow: hidden;
          min-height: 115.5px; } }

      &.small {
        margin-top: 37px;

        .content-bottom {
          margin-top: -35px;
          font-size: 14px; } }

      .content-bottom {
        span {
          margin-right: 20px; }

        p {
          border-bottom: 1px solid #fff;
          padding-bottom: 10px; }

        .subscribe {
          border-radius: 0;
          margin-top: 25px;
          font-size: 20px;
          font-weight: 700;
          width: 100%; } } }

    .subscribe {
      background: #1abc9c;
      color: #fff; } }

  #step {
    background: $clouds; }

  #instrument {
    background: #fff;
    padding-top: 20px;
    padding-bottom: 50px;

    .container {
 }      // max-width: 1000px

    .item {
      text-align: center;
      margin-bottom: 50px;

      p {
        font-size: 1.3em; }

      img {
        height: auto;
        width: auto;
        max-height: 120px;
        max-width: 120px;
        margin-bottom: 25px; } } }

  #last-section {
    background: #f5f5f5;
    padding: 20px 0;

    .container {
      max-width: 1100px; }

    .item {
      padding-bottom: 20px; }

    .news-date {
      color: $concrete; }

    .news-title {
      color: $asbestos;
      font-weight: 700;
      font-size: 1.1em; }

    a {
      &:hover {
        text-decoration: none; } }

    .testimonial-wrapper {
      margin-top: 40px;
      background: #fff;
      border-radius: 4px;
      padding: 25px 40px;

      #testimonial {
        margin-top: 25px;

        .item {
          line-height: 30px;
          font-size: 17px;
          color: $concrete;
          max-height: 117.5px;
          overflow-y: hidden;
          margin-bottom: 20px;
          text-align: justify; } }

      .fa {
        color: $baseColor; }

      .left {
        margin-right: 10px; } }

    .testimonial-identity {
      margin-top: -25px;
      margin-left: 25px;

      .name-cont {
        .name {
          font-weight: 700;
          position: absolute;
          top: 35px;
          left: 20px; } }

      .img-cont {
        img {
          border-radius: 100%;
          border: 5px solid #fff;
          height: 100px;
          width: 100px;
          object-fit: cover; } } } } }

.mfp-counter {
  display: none; }
