#sidebar {
  max-width: 7%;
  min-width: 5%;
  background: $sunflower;
  position: fixed;
  height: 100%;
  right: 0;
  top: 0;
  transition: all 0.2s ease;
  padding-top: 120px;

  .content {
    position: relative;
    z-index: 1000;
    height: 100%; }

  .icon-bar {
    width: 100%;
    background: $yellow;

    p {
      margin: 0; }

    .item {
      border-bottom: 1px solid darken($yellow, 10%);
      display: block;
      text-align: center;
      padding: 14px 16px;
      transition: all 0.3s ease;
      color: #333;

      .item-caption {
        max-height: 20px;
        overflow: hidden; }

      &:hover, &:focus {
        text-decoration: none;
        background-color: darken($yellow, 50%);
        color: #fff;
        outline: 0;
        border-bottom: 1px solid #010100; } } }

  #sidebar-content {
    background: #1F1F1F;
    color: #fff;
    position: fixed;
    top: 0;
    padding-top: 170px;
    height: 100%;
    right: -17%;
    width: 24%;
    z-index: 1;
    transition: all 0.4s ease;
    overflow-y: auto;

    &.open {
      right: 6%;
      transition: all 0.4s ease; }

    .content-head {
      background: #DE1B1B;
      padding: 10px;
      text-align: center;
      color: #fff;
      font-size: 16px;
      top: 121px;
      position: fixed;
      width: 310px; }

    .content-main {
      form {
        margin-left: auto;
        margin-right: auto;
        width: 90%;

        a {
          color: $baseColor; }

        input[type=file] {
          visibility: hidden;
          display: none; }

        .type-input {
          border: 1px dashed #fff;
          display: block;
          text-align: center;
          padding: 20px;
          font-weight: normal;
          cursor: pointer; }

        option {
          padding: 5px;

          &:hover, &:checked {
            box-shadow: 0 0 10px 100px $baseColor inset; } } }

      .select2-container--default .select2-selection--single {
        background-color: #fff;
        border: 1px solid #ccc;
        border-radius: 0;
        font-size: 14px;
        height: 34px;
        padding: 3px 5px; }

      .select2-container--default .select2-selection--single .select2-selection__arrow {
        height: 34px; }

      .input-group-addon:first-child {
        border-radius: 0; }

      input {
        &:required {
          border: 1px solid $turqoise; }

        &.has-error {
          border: 1px solid $pome; } }

      .form-control {
        border-radius: 0;
        box-shadow: none;
        background: #fafafa; }

      .btn {
        text-transform: uppercase;
        font-weight: 700;
        transition: all 0.4s ease;
        border-radius: 0; }

      .btn-submit {
        background: $baseColor;
        color: #fff;
        z-index: 2;

        &:hover {
          background: darken($baseColor, 10%); } }

      .contact-wrapper {
        padding-left: 20px;
        padding-right: 20px;

        [class^="col-md-"] {
          min-height: auto; }

        .contact-title {
          font-weight: 700;
          color: $baseColor; }

        .contact-subtitle {
          font-weight: 700; } } } } }

.ui-state-active:before {
  left: 0;
  top: -100%;
  border: solid transparent;
  border-top-width: medium;
  border-right-width: medium;
  border-bottom-width: medium;
  border-left-width: medium;
  border-top-color: transparent;
  border-right-color: transparent;
  border-bottom-color: transparent;
  border-left-color: transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-color: rgba(136, 183, 213, 0);
  border-left-color: rgba(136, 183, 213, 0);
  border-left-color: #1f1f1f;
  border-width: 10px;
  margin-top: -10px; }
