.kurs-wrapper {
  padding: 10px 10px;

  span:not(.kurs-title) {
    font-weight: normal;
    font-size: 0.7em;
    text-align: right;
    float: right;
    margin-top: 10px; }

  table {
    font-size: 12px;
    border: none;

    td, th {
      text-align: center; }

    thead {
      color: $sunflower;
      text-transform: uppercase;

      th {
        &:first-child {
          border-left: none; }

        &:last-child {
          border-right: none; } } }

    tbody {
      td {
        font-size: 13px;

        &:first-child {
          border-left: none; }

        &:last-child {
          border-right: none; } }

      tr:last-child td {
        border-bottom: none; }

      img {
        height: 20px;
        width: 20px;
        margin-right: 5px;
        margin-bottom: 2px; } } } }

.banner {

  img {
    display: block;
    max-width: 100%;
    width: 100%;
    height: 200px;
    object-fit: cover; } }

.latest-news {

  a:hover {
    text-decoration: none; }

  .item {
    margin-bottom: 25px;

    .news-desc {
      font-size: .9em;
      text-align: justify; } } }

.img-header {

  img {
    min-width: 100%;
    max-height: 250px;
    object-fit: cover; } }

.article {

  .article-writer {
    color: $concrete; }

  .article-content {
    font-size: 16px; } }
